@media (max-width: 767px) {
  .main-header-menu-icon {
    margin-right: 15px;
  }

  .side-header .icon-logo {
    margin-right: 0;
  }

  .main-sidebar-show .side-menu {
    top: 0px;
  }
}

@media (min-width: 992px) {
	.side-header .main-logo{
		color: inherit;
		margin-right: 0;
		font-size: 1.25rem;
		white-space: nowrap;
		font-weight: 600;
		padding: 0;
		transition: .3s opacity;
		line-height: 2.9rem;
		min-width: 200px;
		text-align: center;
		text-align: center;
	}
	.main-content.side-content {
		margin-right: 240px;
	}
	.main-sidebar.side-menu {
		width: 240px;
	}
	.main-footer{
		margin-right:240px;
	}
}

@media (min-width: 768px) {
  
  .main-sidebar-hide {
    .side-menu {
      right: 0;
      width: 100px;
      display: block;
      transform: translateX(0);
      visibility: visible;
    }

    .main-content.side-content {
      margin-right: 100px;
    }

    .nav-label {
      display: none;
    }

    .sidemenu-label {
      display: block;
      font-size: 10px;
    }

    .nav-item {
      width: 100%;
    }

    .text-icon {
      display: none;
    }

    .iconbtn {
      display: block;
    }

    .main-sidebar-body i.angle {
      display: none;
    }

    .side-menu {
      .nav-link {
        display: block;
        margin: 0 auto;
        padding: 10px 0;
        text-align: center;
        margin-bottom: 20px;
      }

      .main-sidebar-body .nav-link i {
        margin-left: 0;
      }

      .main-logo {
        .desktop-logo {
          display: none;
        }

        .icon-logo {
          display: block;
          margin: 0 auto;
          text-align: center;
        }
      }
    }

    .main-sidebar-body .nav-item.show > .nav-sub {
      display: none;
    }

    .main-footer {
      margin-right: 0;
    }

    .side-menu {
      .main-sidebar-body .nav-link i {
        font-size: 20px;
      }

      i.angle {
        font-size: 12px !important;
      }
    }
  }

  /*main-sidebar-open*/

  .main-sidebar-open {
    .side-menu {
      right: 0;
      width: 240px;
    }

    .nav-label, .sidemenu-label, .text-icon {
      display: block;
    }

    .iconbtn {
      display: none;
    }

    .main-sidebar-body i.angle {
      display: block;
    }

    .side-menu .nav-link {
      display: flex;
    }

    .main-sidebar-body .side-menu .nav-link {
      margin-left: 10px;
    }

    .side-menu .nav-link {
      margin-bottom: 0px;
    }

    .sidemenu-label {
      font-size: 14px;
    }

    .side-menu .main-logo {
      .desktop-logo {
        display: block;
        margin: 0 auto;
        text-align: center;
      }

      .icon-logo {
        display: none;
      }
    }

    .main-sidebar-body .nav-item.show > .nav-sub {
      display: block;
    }

    .side-menu {
      .nav-item.show i.angle {
        -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
        transform: rotate(-90deg);
        position: absolute;
        top: 20px;
        left: -15px;
      }

      .main-sidebar-body .nav-link {
        i {
          margin-right: 5px;
          font-size: 18px;
        }

        margin-left: 10px;
      }

      i.angle {
        font-size: 12px;
      }
    }
  }
}
@media (max-width: 991.98px) {
	.main-sidebar-show {
		overflow: inherit;
	}
	.main-sidebar-show .main-sidebar {
		display: flex;
		transform: translateX(0);
		visibility: visible;
    transition: right 0.3s ease, width 0.3s ease;
    z-index: 1000;
	}
	.main-sidebar-show .main-navbar-backdrop {
		visibility: visible;
		opacity: 1;
	}
}
@media (min-width: 1200px){
	.side-content{
		margin-right: 240px;
	}
}

@media(max-width:992px) and (min-width:767px){
	.main-header-menu-icon {
		margin-right: 15px;
	}
}



